// Hierarquia de roles
export const ROLE_HIERARCHY = {
  'ADMIN': 4,
  'MANAGER': 3,
  'LEADER': 2,
  'OPERATOR': 1
};

// Funo para verificar se o usurio tem permisso para acessar uma rota
export const hasRoutePermission = (userRole, route) => {
  const allowedRoles = ROUTE_PERMISSIONS[route];
  
  if (!allowedRoles) {
    return true; // Se no h restrio definida, permite acesso
  }
  
  return allowedRoles.includes(userRole);
};

// Funo para verificar hierarquia de roles
export const hasRolePermission = (userRole, requiredRole) => {
  return ROLE_HIERARCHY[userRole] >= ROLE_HIERARCHY[requiredRole];
};

