import { useState, useEffect, useCallback } from 'react';
import { useAuth } from './useAuth';
import api from '../services/api';

export const useMachinePermissions = () => {
  const { user, isAuthenticated, isLoading: authLoading } = useAuth();
  const [permissions, setPermissions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Carregar permisses do usurio
  const loadUserPermissions = useCallback(async () => {
    console.log(' useMachinePermissions: loadUserPermissions chamado', { userId: user?.id, role: user?.role });
    
    if (!user?.id) {
      console.log(' useMachinePermissions: Usurio no tem ID');
      setPermissions([]);
      setLoading(false);
      return;
    }

    // Admins e Managers tm acesso a todas as mquinas
    if (['ADMIN', 'MANAGER'].includes(user.role)) {
      console.log(' useMachinePermissions: Usurio  ADMIN/MANAGER - sem filtro');
      setPermissions([]);
      setLoading(false);
      return;
    }

    try {
      setLoading(true);
      setError(null);
      
      console.log(' useMachinePermissions: Fazendo chamada para API de permisses');
      const response = await api.get(`/permissions?userId=${user.id}`);
      const userPermissions = response.data.data || [];
      
      console.log(' useMachinePermissions: Permisses carregadas:', userPermissions.length);
      setPermissions(userPermissions);
    } catch (err) {
      console.error(' useMachinePermissions: Erro ao carregar permisses do usurio:', err);
      setError('Erro ao carregar permisses');
      setPermissions([]);
    } finally {
      setLoading(false);
    }
  }, [user?.id, user?.role]);

  // Carregar permisses quando o usurio mudar e estiver autenticado
  useEffect(() => {
    if (isAuthenticated && !authLoading && user?.id) {
      loadUserPermissions();
    }
  }, [loadUserPermissions, isAuthenticated, authLoading, user?.id]);

  // Verificar se o usurio tem permisso para uma mquina especfica
  const hasPermissionForMachine = useCallback((machineId, permissionType = 'canView') => {
    if (!user) {
      return false;
    }
    
    // Admins e Managers tm acesso total
    if (['ADMIN', 'MANAGER'].includes(user.role)) {
      return true;
    }

    // Verificar permisses especficas do operador
    const permission = permissions.find(p => p.machineId === machineId);
    const hasPermission = permission ? permission[permissionType] : false;
    
    return hasPermission;
  }, [user, permissions]);

  // Filtrar mquinas baseado nas permisses do usurio
  const filterMachinesByPermissions = useCallback((machines, permissionType = 'canView') => {
    console.log(' useMachinePermissions: filterMachinesByPermissions chamado', { 
      machinesCount: machines?.length, 
      userRole: user?.role,
      permissionsCount: permissions?.length,
      permissionType 
    });
    
    if (!user) {
      console.log(' useMachinePermissions: Usurio no encontrado');
      return [];
    }
    
    // Admins e Managers veem todas as mquinas
    if (['ADMIN', 'MANAGER'].includes(user.role)) {
      console.log(' useMachinePermissions: Usurio ADMIN/MANAGER - retornando todas as mquinas:', machines?.length);
      return machines;
    }

    // Filtrar mquinas para operadores baseado em suas permisses
    const filteredMachines = machines.filter(machine => {
      const hasPermission = hasPermissionForMachine(machine.id, permissionType);
      console.log(` useMachinePermissions: Mquina ${machine.id} (${machine.name}) - Permisso ${permissionType}: ${hasPermission}`);
      return hasPermission;
    });
    
    console.log(' useMachinePermissions: Mquinas filtradas:', filteredMachines.length);
    return filteredMachines;
  }, [user, hasPermissionForMachine, permissions]);

  // Obter lista de IDs de mquinas que o usurio pode acessar
  const getAccessibleMachineIds = useCallback((permissionType = 'canView') => {
    if (!user) return [];
    
    // Admins e Managers tm acesso a todas (retorna array vazio para indicar "todas")
    if (['ADMIN', 'MANAGER'].includes(user.role)) {
      return [];
    }

    // Retornar IDs das mquinas que o operador pode acessar
    return permissions
      .filter(p => p[permissionType])
      .map(p => p.machineId);
  }, [user, permissions]);

  // Verificar se o usurio tem pelo menos uma permisso
  const hasAnyMachinePermission = useCallback(() => {
    if (!user) {
      return false;
    }
    
    // Admins e Managers sempre tm permisso
    if (['ADMIN', 'MANAGER'].includes(user.role)) {
      return true;
    }

    // Para operadores, verificar se tem pelo menos uma permisso
    return permissions.length > 0;
  }, [user, permissions]);

  // Obter estatsticas de permisses
  const getPermissionStats = useCallback(() => {
    if (!user) return { total: 0, canView: 0, canOperate: 0, canEdit: 0 };
    
    // Admins e Managers tm acesso total (no calculamos estatsticas)
    if (['ADMIN', 'MANAGER'].includes(user.role)) {
      return { total: -1, canView: -1, canOperate: -1, canEdit: -1 }; // -1 indica "todas"
    }

    return {
      total: permissions.length,
      canView: permissions.filter(p => p.canView).length,
      canOperate: permissions.filter(p => p.canOperate).length,
      canEdit: permissions.filter(p => p.canEdit).length
    };
  }, [user, permissions]);

  // Recarregar permisses
  const refreshPermissions = useCallback(() => {
    loadUserPermissions();
  }, [loadUserPermissions]);

  return {
    permissions,
    loading,
    error,
    hasPermissionForMachine,
    filterMachinesByPermissions,
    getAccessibleMachineIds,
    hasAnyMachinePermission,
    getPermissionStats,
    refreshPermissions
  };
};

export default useMachinePermissions;